
.home-page{
  width: 100vw;
  height: 100vh;
  
}
.home-page >.home-navbar{
  position: s!important;
  width: 100vw;
  background-color: #050505;
}
.first-section{
  margin-top: 0px;
  /* display: flex; */
  background-color: rgb(17, 17, 17);
  opacity: 0.8;
  background-color: rgb(73, 72, 72);
  background-image: radial-gradient(circle at center center, #ffffff, #ffffff), repeating-radial-gradient(circle at center center, #050505, #050505, 10px, transparent 20px, transparent 10px);
  background-blend-mode: multiply;
  background-position: center;
  height: 100vh;
  width: 100vw;
  
}
.first-heading{
  display: flex;
  color: rgb(251, 249, 249);
  font-size: 95px;
  font-weight: 300!important;
  /* margin-top: 20%; */
