  margin-left: auto;
  margin-right: auto;
 max-width: 55%;
}
.started-btn{
  background-color: rgb(6, 242, 140);
  height: 45px;
  width: max-content;
  padding: 7px 18px;
  border-radius: 100px;
  color: rgb(5, 5, 5);
  border: none;
  margin-bottom: 12%;
  margin-left: calc(50% - 110px);
  margin-right: calc(50% - 110px);
  text-align: center;
  
}
